import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import logo from '../assets/logo.png'; 

function Recepcion() {
  const navigate = useNavigate();
  const [showFormModal, setShowFormModal] = useState(false);
  const [showSuccessModal, setShowSuccessModal] = useState(false);
  const [showReportModal, setShowReportModal] = useState(false);
  const [showReportSuccessModal, setShowReportSuccessModal] = useState(false);

  const handleNuevaCarga = () => {
    setShowFormModal(true);
  };

  const handleGenerarReporte = () => {
    setShowReportModal(true);
  };

  const handleCerrarSesion = () => {
    navigate('/');
  };

  const handleRegistrar = () => {
    setShowFormModal(false);
    setShowSuccessModal(true);
  };

  const handleCerrarSuccessModal = () => {
    setShowSuccessModal(false);
  };

  const handleRegresarDesdeFormulario = () => {
    setShowFormModal(false);
  };

  const handleGenerarDesdeModal = () => {
    setShowReportModal(false);
    setShowReportSuccessModal(true);
  };

  const handleCerrarReportSuccessModal = () => {
    setShowReportSuccessModal(false);
  };

  const handleRegresarDesdeReportModal = () => {
    setShowReportModal(false);
  };

  return (
    <div style={{
      minHeight: "100vh",
      background: "linear-gradient(135deg, #000000, #32004f, #ff5722)",
      backgroundSize: "cover",
      color: "#fff",
      display: "flex",
      flexDirection: "column"
    }}>
      {/* Navbar actualizado */}
      <div style={{
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        padding: "1rem 2rem",
        backgroundColor: "#1f1f1f",
      }}>
        <div style={{ display: "flex", alignItems: "center", gap: "2rem" }}>
          <div style={{ fontSize: "1.5rem", fontWeight: "bold" }}>
            <img src={logo} alt="ISL Logo" style={{ height: "35px", marginRight: "1rem" }} />
          </div>
          <div style={{ cursor: "pointer" }} onClick={() => navigate('/recepcion')}>
            Módulo de RECEPCIÓN
          </div>
          <div style={{ cursor: "pointer" }} onClick={() => navigate('/transporte')}>
            Módulo de TRANSPORTE
          </div>
          <div style={{ cursor: "pointer" }} onClick={() => navigate('/despacho')}>
            Módulo de DESPACHO
          </div>
          <div style={{ cursor: "pointer" }} onClick={() => navigate('/bultos')}>Módulo de Gestión de BULTOS</div>
          <div style={{ cursor: "pointer" }} onClick={() => navigate('/usuarios')}>Módulo de Gestión de USUARIOS</div>
        </div>
        <button
          onClick={handleCerrarSesion}
          style={{
            backgroundColor: "red",
            border: "none",
            padding: "0.5rem 1rem",
            borderRadius: "0.5rem",
            cursor: "pointer",
            fontWeight: "bold",
            color: "#fff"
          }}>
          Cerrar Sesión
        </button>
      </div>

      {/* Tabla y botones */}
      <div style={{
        flex: 1,
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        padding: "2rem"
      }}>
        <h1 style={{ marginBottom: "2rem" }}>INGRESA UNA CARGA</h1>

        {/* Tabla */}
        <div style={{
          overflowX: "auto",
          backgroundColor: "#fff",
          borderRadius: "1rem",
          padding: "1rem",
          color: "#000",
          marginBottom: "2rem",
          maxWidth: "100%"
        }}>
          <table style={{ borderCollapse: "collapse", width: "100%" }}>
            <thead>
              <tr style={{ backgroundColor: "#eee", color: "#000" }}>
                <th style={cellStyle}>Fecha de Carga</th>
                <th style={cellStyle}>Fecha de Recepción</th>
                <th style={cellStyle}>Código de Local</th>
                <th style={cellStyle}>Código de Bulto</th>
                <th style={cellStyle}>Estado de Recepción</th>
                <th style={cellStyle}>Estado de Transporte</th>
              </tr>
            </thead>
            <tbody>
              {Array.from({ length: 5 }).map((_, index) => (
                <tr key={index}>
                  <td style={cellStyle}>24/04/2025</td>
                  <td style={cellStyle}>25/04/2025</td>
                  <td style={cellStyle}>4440</td>
                  <td style={cellStyle}>BANSA0401891{index}</td>
                  <td style={cellStyle}>EN BUEN ESTADO</td>
                  <td style={cellStyle}>EN ALMACÉN</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        {/* Botones */}
        <div style={{ display: "flex", gap: "2rem" }}>
          <button
            onClick={handleNuevaCarga}
            style={buttonGreen}>
            INGRESAR NUEVA CARGA
          </button>

          <button
            onClick={handleGenerarReporte}
            style={buttonGreen}>
            GENERAR REPORTE
          </button>
        </div>
      </div>

      {/* Modal Ingresar Nueva Carga */}
      {showFormModal && (
        <div style={modalOverlay}>
          <div style={modalContent}>
            <img src="https://img.icons8.com/color/96/000000/delivery.png" alt="Truck" style={{ margin: '1rem auto' }} />
            <h2 style={{ color: "#00ff95" }}>Datos de la nueva carga</h2>

            <input style={inputStyle} type="text" placeholder="Fecha de la carga" />
            <input style={inputStyle} type="text" placeholder="Código de la carga" />
            <input style={inputStyle} type="text" placeholder="Placa de la Carreta" />
            <input style={inputStyle} type="text" placeholder="Dueño de la Carreta" />
            <input style={inputStyle} type="file" accept=".xlsx, .xls" />

            <div style={{ display: "flex", flexDirection: "column", gap: "1rem", marginTop: "1.5rem" }}>
              <button onClick={handleRegistrar} style={registerButton}>Registrar</button>
              <button onClick={handleRegresarDesdeFormulario} style={backButtonInsideModal}>Regresar</button>
            </div>
          </div>
        </div>
      )}

      {/* Modal Confirmación Nueva Carga */}
      {showSuccessModal && (
        <div style={modalOverlay}>
          <div style={modalContent}>
            <h2>DATOS CARGADOS CORRECTAMENTE</h2>
            <img src="https://img.icons8.com/color/96/000000/ok.png" alt="Success" style={{ margin: '1rem auto' }} />
            <button onClick={handleCerrarSuccessModal} style={registerButton}>Aceptar</button>
          </div>
        </div>
      )}

      {/* Modal Seleccionar Carga para Reporte */}
      {showReportModal && (
        <div style={modalOverlay}>
          <div style={modalContent}>
            <h2 style={{ color: "#00ff95" }}>SELECCIONA CARGA</h2>

            <input style={inputStyle} type="text" placeholder="Carga" />

            <div style={{ display: "flex", flexDirection: "column", gap: "1rem", marginTop: "1.5rem" }}>
              <button onClick={handleGenerarDesdeModal} style={registerButton}>Generar</button>
              <button onClick={handleRegresarDesdeReportModal} style={backButtonInsideModal}>Regresar</button>
            </div>
          </div>
        </div>
      )}

      {/* Modal Confirmación Reporte Generado */}
      {showReportSuccessModal && (
        <div style={modalOverlay}>
          <div style={modalContent}>
            <h2>REPORTE GENERADO</h2>
            <img src="https://img.icons8.com/color/96/000000/ok.png" alt="Success" style={{ margin: '1rem auto' }} />
            <button onClick={handleCerrarReportSuccessModal} style={registerButton}>Aceptar</button>
          </div>
        </div>
      )}
    </div>
  );
}

// Estilos auxiliares
const cellStyle = {
  border: "1px solid #ccc",
  padding: "0.5rem",
  textAlign: "center"
};

const modalOverlay = {
  position: "fixed",
  top: 0, left: 0,
  width: "100%", height: "100%",
  backgroundColor: "rgba(0,0,0,0.6)",
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
  zIndex: 1000
};

const modalContent = {
  backgroundColor: "#333",
  padding: "2rem",
  borderRadius: "1rem",
  textAlign: "center",
  width: "90%",
  maxWidth: "400px",
  color: "#fff"
};

const inputStyle = {
  width: "100%",
  padding: "0.8rem",
  margin: "0.5rem 0",
  borderRadius: "0.5rem",
  border: "none",
  backgroundColor: "#444",
  color: "#fff",
  outline: "none",
  boxSizing: "border-box"
};

const registerButton = {
  width: "100%",
  padding: "0.8rem",
  backgroundColor: "#00ff95",
  border: "none",
  borderRadius: "0.5rem",
  fontWeight: "bold",
  color: "#000",
  cursor: "pointer"
};

const backButtonInsideModal = {
  width: "100%",
  padding: "0.8rem",
  backgroundColor: "red",
  border: "none",
  borderRadius: "0.5rem",
  fontWeight: "bold",
  color: "#fff",
  cursor: "pointer"
};

const buttonGreen = {
  padding: "0.8rem 2rem",
  backgroundColor: "#00ff95",
  border: "none",
  borderRadius: "0.5rem",
  color: "#000",
  fontWeight: "bold",
  cursor: "pointer"
};

export default Recepcion;
