import { useState } from "react";
import { useNavigate } from "react-router-dom";
import logo from '../assets/logo.png'; 

function Usuarios() {
  const navigate = useNavigate();

  const [showFormulario, setShowFormulario] = useState(false);
  const [showConfirmacion, setShowConfirmacion] = useState(false);
  const [showGestionEstado, setShowGestionEstado] = useState(false);
  const [accionUsuario, setAccionUsuario] = useState(null); // 'activar' | 'desactivar'

  const navLink = {
    margin: "0 1rem",
    cursor: "pointer",
    fontWeight: "bold",
  };

  const inputStyle = {
    padding: "0.8rem",
    backgroundColor: "#444",
    border: "none",
    borderRadius: "0.5rem",
    color: "#ccc",
    width: "100%",
    marginBottom: "1rem"
  };

  const tableCell = {
    border: "1px solid #ccc",
    padding: "0.5rem",
    textAlign: "center",
    backgroundColor: "#fff",
    color: "#000",
  };

  return (
    <div style={{ minHeight: "100vh", background: "linear-gradient(to right, #1a0536, #d23b20)", color: "#fff" }}>
      {/* Navbar */}
      <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", backgroundColor: "#222", padding: "1rem" }}>
        <div style={{ display: "flex", alignItems: "center" }}>
          <img src={logo} alt="ISL Logo" style={{ height: "35px", marginRight: "1rem" }} />
          <div style={navLink} onClick={() => navigate('/recepcion')}>Módulo de RECEPCIÓN</div>
          <div style={navLink} onClick={() => navigate('/transporte')}>Módulo de TRANSPORTE</div>
          <div style={navLink} onClick={() => navigate('/despacho')}>Módulo de DESPACHO</div>
          <div style={navLink} onClick={() => navigate('/bultos')}>Módulo de Gestión de BULTOS</div>
          <div style={navLink} onClick={() => navigate('/usuarios')}>Módulo de Gestión de USUARIOS</div>
        </div>
        <button onClick={() => navigate('/')} style={{ backgroundColor: "red", border: "none", padding: "0.8rem 1.2rem", borderRadius: "0.5rem", fontWeight: "bold", color: "#fff", cursor: "pointer" }}>
          Cerrar Sesión
        </button>
      </div>

      {/* Contenido */}
      {!showFormulario && !showConfirmacion && !showGestionEstado && (
        <div style={{ padding: "2rem", display: "flex", flexDirection: "column", alignItems: "center" }}>
          <h1 style={{ textAlign: "center", marginBottom: "2rem" }}>GESTIONAR USUARIOS</h1>

          <div style={{ overflowX: "auto", marginBottom: "2rem" }}>
            <table style={{ borderCollapse: "collapse", width: "100%" }}>
              <thead>
                <tr style={{ backgroundColor: "#cce5ff", color: "#000" }}>
                  <th style={tableCell}>usuario</th>
                  <th style={tableCell}>password</th>
                  <th style={tableCell}>rol</th>
                  <th style={tableCell}>estado</th>
                </tr>
              </thead>
              <tbody>
                {[
                  ["HArnao", "************", "Administrador", "Activo"],
                  ["JJimenez", "************", "Operaciones", "Activo"],
                  ["GAlayo", "************", "Operaciones", "Inactivo"],
                  ["GGutierrez", "************", "Atarama", "Inactivo"],
                  ["FChavez", "************", "Atarama", "Activo"],
                ].map((row, i) => (
                  <tr key={i}>{row.map((cell, j) => <td key={j} style={tableCell}>{cell}</td>)}</tr>
                ))}
              </tbody>
            </table>
          </div>

          <div style={{ display: "flex", gap: "2rem" }}>
            <button onClick={() => setShowFormulario(true)} style={{ backgroundColor: "#00ff95", padding: "1rem 2rem", border: "none", borderRadius: "0.5rem", fontWeight: "bold", color: "#000", cursor: "pointer" }}>
              REGISTRAR NUEVO USUARIO
            </button>
            <button onClick={() => setShowGestionEstado(true)} style={{ backgroundColor: "#00ff95", padding: "1rem 2rem", border: "none", borderRadius: "0.5rem", fontWeight: "bold", color: "#000", cursor: "pointer" }}>
              DESACTIVAR/ACTIVAR USUARIO
            </button>
          </div>
        </div>
      )}

      {/* Formulario */}
      {showFormulario && !showConfirmacion && (
        <div style={{ padding: "2rem", display: "flex", justifyContent: "center" }}>
          <div style={{ backgroundColor: "#333", padding: "2rem", borderRadius: "1rem", width: "90%", maxWidth: "400px", display: "flex", flexDirection: "column", alignItems: "center" }}>
            <h2 style={{ color: "#00bfff", textAlign: "center" }}>Nuevo usuario</h2>
            <p style={{ textAlign: "center", marginBottom: "1.5rem" }}>Ingresá los datos de la nueva sesión</p>
            <input style={inputStyle} placeholder="Ingresá el nombre de usuario" />
            <input style={inputStyle} placeholder="Contraseña" type="password" />
            <input style={inputStyle} placeholder="Confirmar Contraseña" type="password" />
            <select style={inputStyle}><option value="">Tipo de Usuario</option></select>

            <button onClick={() => setShowConfirmacion(true)} style={{ backgroundColor: "#337cff", marginTop: "1rem", padding: "0.8rem", width: "100%", border: "none", borderRadius: "0.5rem", color: "#fff", fontWeight: "bold" }}>
              REGISTRAR
            </button>

            <button onClick={() => setShowFormulario(false)} style={{ backgroundColor: "red", marginTop: "1rem", padding: "0.8rem", width: "100%", border: "none", borderRadius: "0.5rem", color: "#fff", fontWeight: "bold" }}>
              REGRESAR
            </button>
          </div>
        </div>
      )}

      {/* Confirmación registro */}
      {showConfirmacion && (
        <div style={{ padding: "2rem", display: "flex", justifyContent: "center", alignItems: "center" }}>
          <div style={{ backgroundColor: "#333", padding: "2rem", borderRadius: "1rem", textAlign: "center" }}>
            <h2 style={{ marginBottom: "1rem" }}>USUARIO REGISTRADO</h2>
            <div style={{ fontSize: "4rem", color: "#00cc66" }}>✔️</div>
            <button onClick={() => { setShowConfirmacion(false); setShowFormulario(false); }}
              style={{ backgroundColor: "#00ff95", marginTop: "1.5rem", padding: "0.8rem 2rem", border: "none", borderRadius: "0.5rem", fontWeight: "bold", color: "#000", cursor: "pointer" }}>
              Aceptar
            </button>
          </div>
        </div>
      )}

      {/* Activar/Desactivar modal */}
      {showGestionEstado && accionUsuario === null && (
        <div style={{ display: "flex", justifyContent: "center", padding: "2rem" }}>
          <div style={{
            backgroundColor: "#333",
            padding: "2rem",
            borderRadius: "1rem",
            width: "300px",
            textAlign: "center",
            display: "flex",
            flexDirection: "column",
            gap: "1rem"
          }}>
            <h3 style={{ color: "#00ff95", marginBottom: "1rem" }}>SELECCIONA UN USUARIO</h3>
            
            <input
              style={{
                width: "100%",
                padding: "0.8rem",
                backgroundColor: "#444",
                border: "none",
                borderRadius: "0.5rem",
                color: "#ccc",
                boxSizing: "border-box"  // <-- clave para respetar el 100% de ancho
              }}
              placeholder="Usuario"
            />

            <button
              onClick={() => setAccionUsuario("activar")}
              style={{
                backgroundColor: "#00ff95",
                width: "100%",
                padding: "0.8rem",
                border: "none",
                borderRadius: "0.5rem",
                fontWeight: "bold"
              }}
            >
              Activar
            </button>

            <button
              onClick={() => setAccionUsuario("desactivar")}
              style={{
                backgroundColor: "#337cff",
                width: "100%",
                padding: "0.8rem",
                border: "none",
                borderRadius: "0.5rem",
                fontWeight: "bold"
              }}
            >
              Desactivar
            </button>

            <button
              onClick={() => setShowGestionEstado(false)}
              style={{
                backgroundColor: "#ff3333",
                width: "100%",
                padding: "0.8rem",
                border: "none",
                borderRadius: "0.5rem",
                fontWeight: "bold"
              }}
            >
              REGRESAR
            </button>
          </div>
        </div>
      )}

      {/* Resultado Activar o Desactivar */}
      {accionUsuario !== null && (
        <div style={{ padding: "2rem", display: "flex", justifyContent: "center" }}>
          <div style={{ backgroundColor: "#333", padding: "2rem", borderRadius: "1rem", textAlign: "center" }}>
            <h2 style={{ marginBottom: "1rem" }}>{accionUsuario === "activar" ? "Usuario Activado" : "Usuario Desactivado"}</h2>
            <div style={{ fontSize: "4rem", color: "#00cc66" }}>✔️</div>
            <button onClick={() => { setAccionUsuario(null); setShowGestionEstado(false); }}
              style={{ backgroundColor: "#00ff95", marginTop: "1.5rem", padding: "0.8rem 2rem", border: "none", borderRadius: "0.5rem", fontWeight: "bold", color: "#000", cursor: "pointer" }}>
              ACEPTAR
            </button>
          </div>
        </div>
      )}
    </div>
  );
}

export default Usuarios;