import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import logo from '../assets/logo.png';

function Login() {
  const [showPassword, setShowPassword] = useState(false);
  const navigate = useNavigate();

  const togglePasswordVisibility = () => {
    setShowPassword(prev => !prev);
  };

  const handleLogin = () => {
    navigate('/recepcion');
  };

  return (
    <div style={{
      height: "100vh",
      background: "linear-gradient(135deg, #000000, #32004f, #ff5722)",
      backgroundSize: "cover",
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      padding: "1rem",
    }}>
      <div style={{
        backgroundColor: "#2c2c2c",
        padding: "2.5rem 2rem",
        borderRadius: "1rem",
        width: "100%",
        maxWidth: "350px",
        boxShadow: "0 0 30px rgba(0, 0, 0, 0.5)",
        textAlign: "center",
        color: "#fff",
      }}>
        {/* Logo */}
        <div style={{ textAlign: "center", marginBottom: "1.5rem" }}>
          <img src={logo} alt="Logo ISL" style={{ height: "50px" }} />
        </div>

        {/* Bienvenida */}
        <h2 style={{ marginBottom: "0.5rem", color: "#3b82f6" }}>¡Bienvenido!</h2>
        <p style={{ fontSize: "0.9rem", color: "#ccc", marginBottom: "2rem" }}>
          Ingresa tus datos para iniciar sesión en tu cuenta.
        </p>

        {/* Inputs */}
        <input
          type="text"
          placeholder="Ingresa tu usuario"
          style={{
            width: "100%",
            padding: "0.8rem",
            marginBottom: "1rem",
            borderRadius: "0.5rem",
            border: "none",
            backgroundColor: "#444",
            color: "#fff",
            outline: "none",
            boxSizing: "border-box"
          }}
        />

        <div style={{ position: "relative", width: "100%" }}>
          <input
            type={showPassword ? "text" : "password"}
            placeholder="Contraseña"
            style={{
              width: "100%",
              padding: "0.8rem 2.5rem 0.8rem 1rem",
              marginBottom: "1.5rem",
              borderRadius: "0.5rem",
              border: "none",
              backgroundColor: "#444",
              color: "#fff",
              outline: "none",
              boxSizing: "border-box"
            }}
          />
          <button
            onClick={togglePasswordVisibility}
            style={{
              position: "absolute",
              right: "10px",
              top: "50%",
              transform: "translateY(-50%)",
              background: "none",
              border: "none",
              color: "#ccc",
              cursor: "pointer",
              fontSize: "1.1rem"
            }}
            aria-label="Mostrar u ocultar contraseña"
          >
            <i className={showPassword ? "fas fa-eye-slash" : "fas fa-eye"}></i>
          </button>
        </div>

        

        {/* Botón de Ingresar */}
        <button
          onClick={handleLogin}
          style={{
            width: "100%",
            padding: "0.8rem",
            backgroundColor: "#3b82f6",
            color: "#fff",
            border: "none",
            borderRadius: "0.5rem",
            cursor: "pointer",
            fontWeight: "bold",
            fontSize: "1rem",
          }}>
          Ingresar
        </button>
      </div>
    </div>
  );
}

export default Login;
