import { useNavigate } from 'react-router-dom';
import logo from '../assets/logo.png'; 


function Bultos() {
  const navigate = useNavigate();

  const navLink = {
    margin: "0 1rem",
    cursor: "pointer",
    fontWeight: "bold",
  };

  const inputStyle = {
    padding: "0.8rem",
    backgroundColor: "#444",
    border: "none",
    borderRadius: "0.5rem",
    color: "#ccc",
    width: "100%",
  };

  const tableCell = {
    border: "1px solid #ccc",
    padding: "0.5rem",
    textAlign: "center",
    backgroundColor: "#fff",
    color: "#000",
  };

  return (
    <div style={{ minHeight: "100vh", background: "linear-gradient(to right, #1a0536, #d23b20)", color: "#fff" }}>
      {/* Navbar */}
      <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", backgroundColor: "#222", padding: "1rem" }}>
        <div style={{ display: "flex", alignItems: "center" }}>
         <img src={logo} alt="ISL Logo" style={{ height: "35px", marginRight: "1rem" }} />
          <div style={navLink} onClick={() => navigate('/recepcion')}>Módulo de RECEPCIÓN</div>
          <div style={navLink} onClick={() => navigate('/transporte')}>Módulo de TRANSPORTE</div>
          <div style={navLink} onClick={() => navigate('/despacho')}>Módulo de DESPACHO</div>
          <div style={navLink} onClick={() => navigate('/bultos')}>Módulo de Gestión de BULTOS</div>
          <div style={navLink} onClick={() => navigate('/usuarios')}>Módulo de Gestión de USUARIOS</div>
        </div>
        <button onClick={() => navigate('/')} style={{ backgroundColor: "red", border: "none", padding: "0.8rem 1.2rem", borderRadius: "0.5rem", fontWeight: "bold", color: "#fff", cursor: "pointer" }}>
          Cerrar Sesión
        </button>
      </div>

      {/* Contenido */}
      <div style={{ padding: "2rem", display: "flex", flexDirection: "column", alignItems: "center" }}>
        <div style={{ backgroundColor: "#333", padding: "2rem", borderRadius: "1rem", width: "90%", maxWidth: "1200px" }}>
          <h2 style={{ textAlign: "center", color: "#00ff95", marginBottom: "2rem" }}>Gestionar Estado del Bulto</h2>

          {/* Botón Seleccionar Carga */}
          <div style={{ display: "flex", flexDirection: "column", alignItems: "center", marginBottom: "2rem" }}>
            <button style={{ backgroundColor: "#00ff95", padding: "0.8rem 2rem", border: "none", borderRadius: "0.5rem", fontWeight: "bold", marginBottom: "1rem", cursor: "pointer" }}>
              SELECCIONA UNA CARGA
            </button>
            <input style={inputStyle} placeholder="CÓDIGO DE CARGA" />
          </div>

          {/* Filtros */}
          <div style={{ display: "flex", flexWrap: "wrap", gap: "1rem", marginBottom: "2rem", justifyContent: "space-between" }}>
            <div style={{ flex: "1" }}>
              <label>Código de Local:</label>
              <select style={inputStyle}><option>Seleccione</option></select>
            </div>
            <div style={{ flex: "1" }}>
              <label>Fecha de Carga:</label>
              <select style={inputStyle}><option>Seleccione</option></select>
            </div>
            <div style={{ flex: "1" }}>
              <label>Código de Bulto:</label>
              <select style={inputStyle}><option>Seleccione</option></select>
            </div>
            <div style={{ flex: "1" }}>
              <label>Código de Carga:</label>
              <select style={inputStyle}><option>Seleccione</option></select>
            </div>
          </div>

          {/* Tabla */}
          <div style={{ overflowX: "auto", marginBottom: "2rem" }}>
            <table style={{ borderCollapse: "collapse", width: "100%" }}>
              <thead>
                <tr>
                  <th style={tableCell}>Fecha de Carga</th>
                  <th style={tableCell}>Código del Bulto</th>
                  <th style={tableCell}>Código de Local</th>
                  <th style={tableCell}>Código de Carga</th>
                  <th style={tableCell}>Estado de Recepción</th>
                  <th style={tableCell}>Estado de Transporte</th>
                  <th style={tableCell}>Estado de Despacho</th>
                  <th style={tableCell}>Fecha de Despacho</th>
                </tr>
              </thead>
              <tbody>
                {Array.from({ length: 4 }).map((_, index) => (
                  <tr key={index}>
                    <td style={tableCell}>21/02/2025</td>
                    <td style={tableCell}>50934897</td>
                    <td style={tableCell}>4541</td>
                    <td style={tableCell}>OTM00003156</td>
                    <td style={tableCell}>BUEN ESTADO</td>
                    <td style={tableCell}>EN ALMACÉN</td>
                    <td style={tableCell}>-</td>
                    <td style={tableCell}>-</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          {/* Ingresar Fecha */}
          <div style={{ marginBottom: "2rem", textAlign: "center" }}>
            <label style={{ display: "block", marginBottom: "0.5rem" }}>Ingresar fecha de Transporte:</label>
            <input style={inputStyle} placeholder="Fecha de Transporte" />
          </div>

        </div>
      </div>
    </div>
  );
}

export default Bultos;
