import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import logo from '../assets/logo.png'; 

function Transporte() {
  const navigate = useNavigate();
  const [showRegisterModal, setShowRegisterModal] = useState(false);
  const [showEditRouteView, setShowEditRouteView] = useState(false);
  const [showRepeatModal, setShowRepeatModal] = useState(false);

  const handleCerrarSesion = () => {
    navigate('/');
  };

  const handleAbrirRegistroRuta = () => {
    setShowRegisterModal(true);
  };

  const handleRegresarDesdeModal = () => {
    setShowRegisterModal(false);
  };

  const handleClickLapiz = () => {
    setShowRegisterModal(false);
    setShowEditRouteView(true);
  };

  const handleRegresarDesdeEditarRuta = () => {
    setShowEditRouteView(false);
    setShowRegisterModal(true);
  };

  const handleRegistrarRuta = () => {
    setShowRegisterModal(false);
    setShowRepeatModal(true);
  };

  const handleRepetirRegistro = () => {
    setShowRepeatModal(false);
    setShowRegisterModal(true);
  };

  const handleNoRepetirRegistro = () => {
    setShowRepeatModal(false);
  };

  return (
    <div style={{
      minHeight: "100vh",
      background: "linear-gradient(135deg, #000000, #32004f, #ff5722)",
      backgroundSize: "cover",
      color: "#fff",
      display: "flex",
      flexDirection: "column"
    }}>
      {/* Navbar actualizado */}
      <div style={{
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        padding: "1rem 2rem",
        backgroundColor: "#1f1f1f",
      }}>
        <div style={{ display: "flex", alignItems: "center", gap: "2rem" }}>
          <div style={{ fontSize: "1.5rem", fontWeight: "bold" }}>
            <img src={logo} alt="ISL Logo" style={{ height: "35px", marginRight: "1rem" }} />
          </div>
          <div style={{ cursor: "pointer" }} onClick={() => navigate('/recepcion')}>
            Módulo de RECEPCIÓN
          </div>
          <div style={{ cursor: "pointer" }} onClick={() => navigate('/transporte')}>
            Módulo de TRANSPORTE
          </div>
          <div style={{ cursor: "pointer" }} onClick={() => navigate('/despacho')}>
            Módulo de DESPACHO
          </div>
          <div style={{ cursor: "pointer" }} onClick={() => navigate('/bultos')}>Módulo de Gestión de BULTOS</div>
          <div style={{ cursor: "pointer" }} onClick={() => navigate('/usuarios')}>Módulo de Gestión de USUARIOS</div>
        </div>
        <button
          onClick={handleCerrarSesion}
          style={{
            backgroundColor: "red",
            border: "none",
            padding: "0.5rem 1rem",
            borderRadius: "0.5rem",
            cursor: "pointer",
            fontWeight: "bold",
            color: "#fff"
          }}>
          Cerrar Sesión
        </button>
      </div>

      {/* Vista Principal */}
      {!showRegisterModal && !showEditRouteView && !showRepeatModal && (
        <div style={{
          flex: 1,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          padding: "2rem"
        }}>
          <h1 style={{ marginBottom: "2rem" }}>PROGRAMACIÓN DE RUTA</h1>

          {/* Tabla */}
          <div style={{
            overflowX: "auto",
            backgroundColor: "#fff",
            borderRadius: "1rem",
            padding: "1rem",
            color: "#000",
            marginBottom: "2rem",
            maxWidth: "100%"
          }}>
            <table style={{ borderCollapse: "collapse", width: "100%" }}>
              <thead>
                <tr style={{ backgroundColor: "#eee", color: "#000" }}>
                  <th style={cellStyle}>Locales</th>
                  <th style={cellStyle}>ID-Carga (FK)</th>
                  <th style={cellStyle}>ID-Transporte (FK)</th>
                  <th style={cellStyle}>Comentario</th>
                </tr>
              </thead>
              <tbody>
                {Array.from({ length: 5 }).map((_, index) => (
                  <tr key={index}>
                    <td style={cellStyle}>23 / 50 / 259 / 18</td>
                    <td style={cellStyle}>OTM0000031{index}</td>
                    <td style={cellStyle}>TE{index}-328</td>
                    <td style={cellStyle}>Comentario {index}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          {/* Botones */}
          <div style={{ display: "flex", gap: "2rem" }}>
            <button onClick={handleAbrirRegistroRuta} style={buttonGreen}>
              REGISTRAR NUEVA RUTA
            </button>
            <button style={buttonGreen}>
              GENERAR REPORTE DE RUTA
            </button>
          </div>
        </div>
      )}

      {/* Modal Registro de Ruta */}
      {showRegisterModal && (
        <div style={modalOverlay}>
          <div style={modalContent}>
            <img src="https://img.icons8.com/fluency/96/000000/route.png" alt="Ruta" style={{ margin: '1rem auto' }} />
            <h2 style={{ color: "#00ff95" }}>Registro de Ruta</h2>

            <input style={inputStyle} type="text" placeholder="Elegir placa de carro" />
            <div style={{ position: "relative" }}>
              <input style={inputStyle} type="text" placeholder="Rutas por defecto" />
              <button
                onClick={handleClickLapiz}
                style={{
                  position: "absolute",
                  right: "10px",
                  top: "50%",
                  transform: "translateY(-50%)",
                  background: "none",
                  border: "none",
                  fontSize: "1.2rem",
                  cursor: "pointer",
                  color: "#ccc"
                }}
              >
                ✏️
              </button>
            </div>
            <input style={inputStyle} type="text" placeholder="Comentario" />

            <div style={{ display: "flex", gap: "1rem", marginTop: "1.5rem" }}>
              <button onClick={handleRegistrarRuta} style={registerButton}>
                REGISTRAR RUTA
              </button>
              <button onClick={handleRegresarDesdeModal} style={backButtonInsideModal}>
                REGRESAR
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Vista Editar Ruta */}
      {showEditRouteView && (
        <div style={{
          flex: 1,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          padding: "2rem"
        }}>
          <h1>LISTA DE RUTA</h1>

          <div style={{
            backgroundColor: "#333",
            padding: "2rem",
            borderRadius: "1rem",
            marginBottom: "2rem",
            width: "90%",
            maxWidth: "600px"
          }}>
            <table style={{ width: "100%", color: "#fff" }}>
              <thead>
                <tr>
                  <th>Posición</th>
                  <th>Local</th>
                  <th>Acción</th>
                </tr>
              </thead>
              <tbody>
                {[1, 2, 3, 4].map(pos => (
                  <tr key={pos}>
                    <td style={{ textAlign: "center" }}>{pos}</td>
                    <td style={{ textAlign: "center" }}>Local {pos}</td>
                    <td style={{ textAlign: "center" }}>
                      ⬆️ ⬇️ 🗑️
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          <div style={{
            backgroundColor: "#333",
            padding: "2rem",
            borderRadius: "1rem",
            width: "300px"
          }}>
            <h3>Agrega Local</h3>
            <input style={inputStyle} type="text" placeholder="Local" />
            <button style={{ ...buttonGreen, marginTop: "1rem", width: "100%" }}>
              Agregar
            </button>
          </div>

          <div style={{ display: "flex", justifyContent: "center", marginTop: "2rem" }}>
          <button
            onClick={handleRegresarDesdeEditarRuta}
            style={{
                padding: "0.8rem 2rem",
                backgroundColor: "red",
                border: "none",
                borderRadius: "0.5rem",
                fontWeight: "bold",
                color: "#fff",
                cursor: "pointer",
                width: "250px",
                textAlign: "center"
            }}
            > Regresar
             </button>
          </div>
        </div>
      )}

      {/* Modal Repetir Registro */}
      {showRepeatModal && (
        <div style={modalOverlay}>
          <div style={modalContent}>
            <h2 style={{ color: "#00ff95" }}>¿Desea registrar otra ruta?</h2>
            <div style={{ display: "flex", flexDirection: "column", gap: "1rem", marginTop: "1.5rem" }}>
              <button onClick={handleRepetirRegistro} style={registerButton}>
                Sí
              </button>
              <button onClick={handleNoRepetirRegistro} style={backButtonInsideModal}>
                No
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

// Estilos auxiliares
const cellStyle = {
  border: "1px solid #ccc",
  padding: "0.5rem",
  textAlign: "center"
};

const modalOverlay = {
  position: "fixed",
  top: 0, left: 0,
  width: "100%", height: "100%",
  backgroundColor: "rgba(0,0,0,0.6)",
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
  zIndex: 1000
};

const modalContent = {
  backgroundColor: "#333",
  padding: "2rem",
  borderRadius: "1rem",
  textAlign: "center",
  width: "90%",
  maxWidth: "400px",
  color: "#fff"
};

const inputStyle = {
  width: "100%",
  padding: "0.8rem",
  margin: "0.5rem 0",
  borderRadius: "0.5rem",
  border: "none",
  backgroundColor: "#444",
  color: "#fff",
  outline: "none",
  boxSizing: "border-box"
};

const buttonGreen = {
  padding: "0.8rem 2rem",
  backgroundColor: "#00ff95",
  border: "none",
  borderRadius: "0.5rem",
  color: "#000",
  fontWeight: "bold",
  cursor: "pointer"
};

const registerButton = {
  width: "100%",
  padding: "0.8rem",
  backgroundColor: "#00ff95",
  border: "none",
  borderRadius: "0.5rem",
  fontWeight: "bold",
  color: "#000",
  cursor: "pointer"
};

const backButtonInsideModal = {
  width: "100%",
  padding: "0.8rem",
  backgroundColor: "red",
  border: "none",
  borderRadius: "0.5rem",
  fontWeight: "bold",
  color: "#fff",
  cursor: "pointer"
};

export default Transporte;
