import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import logo from '../assets/logo.png'; 


function Despacho() {
  const navigate = useNavigate();

  // Estados para los diferentes modales y formularios
  const [showTipoRegistroModal, setShowTipoRegistroModal] = useState(false);
  const [showFormularioFaltante, setShowFormularioFaltante] = useState(false);
  const [showConfirmacionFaltante, setShowConfirmacionFaltante] = useState(false);
  const [showOtroFaltanteModal, setShowOtroFaltanteModal] = useState(false);
  const [showFormularioIrregularidad, setShowFormularioIrregularidad] = useState(false);
  const [showConfirmacionIrregularidad, setShowConfirmacionIrregularidad] = useState(false);
  const [showOtroIrregularidadModal, setShowOtroIrregularidadModal] = useState(false);
  const [showGestionActas, setShowGestionActas] = useState(false);
  const [showModalReportes, setShowModalReportes] = useState(false);
  const [showConfirmarEntregaCompleta, setShowConfirmarEntregaCompleta] = useState(false);
  const [showExitoEntregaCompleta, setShowExitoEntregaCompleta] = useState(false);


  const handleCerrarSesion = () => {
    navigate('/');
  };

  // Botones principales
  const handleRegistrarEntrega = () => {
    setShowTipoRegistroModal(true);
  };

  // Tipo de Registro Modal
  const handleRegresarDesdeTipoRegistro = () => {
    setShowTipoRegistroModal(false);
  };

  const handleRegistrarFaltante = () => {
    // ocultar todo
    setShowTipoRegistroModal(false);
    setShowFormularioIrregularidad(false);  
    // mostrar el deseado
    setShowFormularioFaltante(true);
  };

  const handleRegistrarIrregularidad = () => {
    setShowTipoRegistroModal(false);
    setShowFormularioFaltante(false);       // ⬅️ ocultar faltante
    setShowFormularioIrregularidad(true);
  };

  // Faltante Formulario
  const handleRegresarDesdeFormularioFaltante = () => {
    setShowFormularioFaltante(false);
    setShowTipoRegistroModal(true);
  };

  const handleSubmitFaltante = () => {
    setShowConfirmacionFaltante(true);
  };

  const handleConfirmarFaltanteSi = () => {
    setShowConfirmacionFaltante(false);
    setShowOtroFaltanteModal(true);
  };

  const handleConfirmarFaltanteNo = () => {
    setShowConfirmacionFaltante(false);
    setShowFormularioFaltante(true);
  };

  const handleOtroFaltanteSi = () => {
    setShowOtroFaltanteModal(false);
    setShowFormularioFaltante(true);
  };

  const handleOtroFaltanteNo = () => {
    setShowFormularioFaltante(false);       // ocultar formulario actual
    setShowOtroFaltanteModal(false);        // cerrar el modal actual
    setShowTipoRegistroModal(true);         // mostrar el selector
  };
    
  // Irregularidad Formulario
  const handleRegresarDesdeFormularioIrregularidad = () => {
    setShowFormularioIrregularidad(false);
    setShowTipoRegistroModal(true);
  };

  const handleSubmitIrregularidad = () => {
    setShowConfirmacionIrregularidad(true);
  };

  const handleConfirmarIrregularidadSi = () => {
    setShowConfirmacionIrregularidad(false);
    setShowOtroIrregularidadModal(true);
  };

  const handleConfirmarIrregularidadNo = () => {
    setShowConfirmacionIrregularidad(false);
    setShowFormularioIrregularidad(true);
  };

  const handleOtroIrregularidadSi = () => {
    setShowOtroIrregularidadModal(false);
    setShowFormularioIrregularidad(true);
  };

  const handleOtroIrregularidadNo = () => {
    setShowFormularioIrregularidad(false);  // ocultar el formulario
    setShowOtroIrregularidadModal(false);   // cerrar el modal
    setShowTipoRegistroModal(true);         // volver al modal de selección
  };

  const handleRegistrarSinFaltantes = () => {
    setShowTipoRegistroModal(false); // ocultamos modal anterior
    setShowConfirmarEntregaCompleta(true); // mostramos modal de confirmación
  };
  
  
  return (
    <div style={mainContainer}>
      {/* Navbar */}
      <div style={navbar}>
        <div style={{ display: "flex", alignItems: "center", gap: "2rem" }}>
          <div style={{ fontSize: "1.5rem", fontWeight: "bold" }}>
            <img src={logo} alt="ISL Logo" style={{ height: "35px", marginRight: "1rem" }} />
          </div>
          <div style={navLink} onClick={() => navigate('/recepcion')}>Módulo de RECEPCIÓN</div>
          <div style={navLink} onClick={() => navigate('/transporte')}>Módulo de TRANSPORTE</div>
          <div style={navLink} onClick={() => navigate('/despacho')}>Módulo de DESPACHO</div>
          <div style={{ cursor: "pointer" }} onClick={() => navigate('/bultos')}>Módulo de Gestión de BULTOS</div>
          <div style={{ cursor: "pointer" }} onClick={() => navigate('/usuarios')}>Módulo de Gestión de USUARIOS</div>
        </div>
        <button onClick={handleCerrarSesion} style={logoutButton}>
          Cerrar Sesión
        </button>
      </div>

      {/* Vista principal (tabla y botones) */}
      {!showTipoRegistroModal && 
       !showFormularioFaltante && 
       !showFormularioIrregularidad &&
       !showConfirmacionFaltante && 
       !showConfirmacionIrregularidad &&
       !showOtroFaltanteModal && 
       !showOtroIrregularidadModal &&
       !showGestionActas && 
       !showModalReportes && (

          <div style={centeredContent}>
            <h1 style={{ marginBottom: "2rem" }}>REGISTRA LA ENTREGA</h1>

            <div style={{ display: "flex", gap: "2rem", marginBottom: "1rem" }}>
              <input style={inputStyle} type="text" placeholder="Selecciona una fecha de carga" />
              <input style={inputStyle} type="text" placeholder="Selecciona un local" />
            </div>

            <div style={tableContainer}>
              <table style={{ borderCollapse: "collapse", width: "100%" }}>
                <thead>
                  <tr style={{ backgroundColor: "#eee", color: "#000" }}>
                    <th style={cellStyle}>Fecha de Carga</th>
                    <th style={cellStyle}>Código de Bulto</th>
                    <th style={cellStyle}>Código de Local</th>
                    <th style={cellStyle}>Código de Carga</th>
                    <th style={cellStyle}>Estado de Recepción</th>
                    <th style={cellStyle}>Estado de Transporte</th>
                    <th style={cellStyle}>Fecha Transporte</th>
                    <th style={cellStyle}>Estado Despacho</th>
                    <th style={cellStyle}>Fecha Despacho</th>
                  </tr>
                </thead>
                <tbody>
                  {Array.from({ length: 4 }).map((_, index) => (
                    <tr key={index}>
                      <td style={cellStyle}>21/04/2025</td>
                      <td style={cellStyle}>BULTO-{index}</td>
                      <td style={cellStyle}>4541</td>
                      <td style={cellStyle}>OTM0000031{index}</td>
                      <td style={cellStyle}>BUEN ESTADO</td>
                      <td style={cellStyle}>EN CAMINO</td>
                      <td style={cellStyle}>22/04/2025</td>
                      <td style={cellStyle}>-</td>
                      <td style={cellStyle}>-</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>

            <div style={{ display: "flex", gap: "2rem" }}>
              <button onClick={handleRegistrarEntrega} style={buttonGreen}>REGISTRAR ENTREGA</button>
              <button onClick={() => setShowGestionActas(true)} style={buttonGreen}>GESTIÓN DE ACTAS</button>
              <button onClick={() => setShowModalReportes(true)} style={buttonGreen}>REPORTES</button>
            </div>
          </div>
        )}
        {showGestionActas && (
            <div style={centeredContent}>
                <h1>VISTA DE LA TABLA ACTAS</h1>

                <div style={{
                    backgroundColor: "#333",
                    padding: "2rem",
                    borderRadius: "1rem",
                    width: "90%",
                    maxWidth: "1000px",
                    marginBottom: "2rem",
                    display: "flex",
                    flexDirection: "column",
                    gap: "1.5rem"
                    }}>
                    <div style={{ display: "flex", justifyContent: "space-between", gap: "1rem" }}>
                        <div style={{ display: "flex", flexDirection: "column" }}>
                        <label style={{ marginBottom: "0.3rem", color: "#ccc" }}>Fecha de la incidencia:</label>
                        <input style={inputStyle} type="text" />
                        </div>
                        <div style={{ display: "flex", flexDirection: "column" }}>
                        <label style={{ marginBottom: "0.3rem", color: "#ccc" }}>Estado:</label>
                        <select style={inputStyle}><option value="">Seleccione</option></select>
                        </div>
                        <div style={{ display: "flex", flexDirection: "column" }}>
                        <label style={{ marginBottom: "0.3rem", color: "#ccc" }}>Código de Bulto:</label>
                        <select style={inputStyle}><option value="">Seleccione</option></select>
                        </div>
                    </div>

                    <div style={{ display: "flex", justifyContent: "space-between", gap: "1rem" }}>
                        <div style={{ display: "flex", flexDirection: "column" }}>
                        <label style={{ marginBottom: "0.3rem", color: "#ccc" }}>Tipo de merma:</label>
                        <select style={inputStyle}><option value="">Seleccione</option></select>
                        </div>
                        <div style={{ display: "flex", flexDirection: "column" }}>
                        <label style={{ marginBottom: "0.3rem", color: "#ccc" }}>Nombre de Local:</label>
                        <select style={inputStyle}><option value="">Seleccione</option></select>
                        </div>
                        <div style={{ flex: 1 }}></div> {/* Espacio vacío para que se alineen */}
                    </div>
                    </div>


                <div style={tableContainer}>
                <table style={{ borderCollapse: "collapse", width: "100%" }}>
                    <thead>
                    <tr style={{ backgroundColor: "#eee", color: "#000" }}>
                        <th style={cellStyle}>Fecha de Registro</th>
                        <th style={cellStyle}>Número de Acta</th>
                        <th style={cellStyle}>Nombre de Local</th>
                        <th style={cellStyle}>Código de Bulto</th>
                        <th style={cellStyle}>Nombre del Bulto</th>
                        <th style={cellStyle}>Cantidad</th>
                        <th style={cellStyle}>Tipo de Merma</th>
                        <th style={cellStyle}>Estado</th>
                        <th style={cellStyle}>Fecha de Regularización</th>
                        <th style={cellStyle}>Foto de Detalle</th>
                        <th style={cellStyle}>Foto de Regularización</th>
                        <th style={cellStyle}>Responsabilidad</th>
                        <th style={cellStyle}>Comentario</th>
                    </tr>
                    </thead>
                    <tbody>
                    {Array.from({ length: 5 }).map((_, index) => (
                        <tr key={index}>
                        <td style={cellStyle}>21/04/2025</td>
                        <td style={cellStyle}>{45 + index}</td>
                        <td style={cellStyle}>Local {index + 1}</td>
                        <td style={cellStyle}>BULTO-{index}</td>
                        <td style={cellStyle}>Producto {index + 1}</td>
                        <td style={cellStyle}>1</td>
                        <td style={cellStyle}>FALTANTE</td>
                        <td style={cellStyle}>PENDIENTE</td>
                        <td style={cellStyle}>-</td>
                        <td style={cellStyle}>-</td>
                        <td style={cellStyle}>-</td>
                        <td style={cellStyle}>OPERADOR</td>
                        <td style={cellStyle}>-</td>
                        </tr>
                    ))}
                    </tbody>
                </table>
                </div>

                <h2 style={{ color: "#00ff95", marginBottom: "1rem" }}>CAMBIAR ESTADO</h2>
                <div style={{
                backgroundColor: "#333",
                padding: "2rem",
                borderRadius: "1rem",
                width: "90%",
                maxWidth: "400px",
                marginBottom: "2rem",
                display: "flex",
                flexDirection: "column",
                gap: "1rem"
                }}>
                <input style={inputStyle} type="text" placeholder="Código de Bulto" />
                <input style={inputStyle} type="text" placeholder="Nuevo Estado" />
                <button style={buttonGreen}>GUARDAR</button>
                </div>

                <div style={{ display: "flex", justifyContent: "center", marginTop: "2rem" }}>
                    <button onClick={() => setShowGestionActas(false)} style={{ ...backButtonInsideModal, width: "200px" }}>
                        REGRESAR
                    </button>
                </div>
            </div>
            )}

        {showModalReportes && (
        <div style={modalOverlay}>
            <div style={modalContent}>
            <h2 style={{ color: "#00ff95" }}>TIPO DE REPORTE</h2>
            <div style={{ display: "flex", flexDirection: "column", gap: "1rem", marginTop: "1.5rem" }}>
                <button style={registerButton}>Estado del Despacho</button>
                <button style={registerButton}>Reporte de Deteriorados</button>
                <button style={registerButton}>Reporte de Discrepancias</button>
                <button style={registerButton}>Reporte de Faltantes</button>
                <button style={{ ...registerButton, backgroundColor: "#ccff00" }}>Reporte General</button>
                <button onClick={() => setShowModalReportes(false)} style={backButtonInsideModal}>REGRESAR</button>
            </div>
            </div>
        </div>
        )}


        {showTipoRegistroModal && (
        <div style={modalOverlay}>
            <div style={modalContent}>
            <h2 style={{ color: "#00ff95" }}>TIPO DE REGISTRO DE ENTREGA</h2>
            <div style={{ display: "flex", flexDirection: "column", gap: "1rem", marginTop: "1.5rem" }}>
                <button onClick={handleRegistrarFaltante} style={registerButton}>REGISTRAR FALTANTE</button>
                <button onClick={handleRegistrarIrregularidad} style={registerButton}>REGISTRAR IRREGULARIDAD</button>
                <button onClick={handleRegistrarSinFaltantes}style={registerButton}>
                  REGISTRAR SIN FALTANTES
                </button>
                <button onClick={handleRegresarDesdeTipoRegistro} style={backButtonInsideModal}>REGRESAR</button>
            </div>
            </div>
        </div>
        )}

        {showFormularioFaltante && (
        <div style={centeredContent}>
            <h1>FORMULARIO DE FALTANTE</h1>
            <div style={formContainer}>
            <input style={inputStyle} type="text" placeholder="Fecha de la Incidencia" />
            <input style={inputStyle} type="text" placeholder="Código del Bulto" />
            <input style={inputStyle} type="text" placeholder="Número de Acta" />
            <input style={inputStyle} type="text" placeholder="Nombre del Auxiliar" />
            <input style={inputStyle} type="text" placeholder="Nombre del Bulto" />
            <div style={cameraBox}>📷</div>
            </div>
            <div style={{ display: "flex", gap: "2rem" }}>
            <button onClick={handleSubmitFaltante} style={buttonGreen}>REGISTRAR</button>
            <button onClick={handleRegresarDesdeFormularioFaltante} style={backButtonInsideModal}>REGRESAR</button>
            </div>
        </div>
        )}

        {showConfirmacionFaltante && (
        <div style={modalOverlay}>
            <div style={modalContent}>
            <h2 style={{ color: "#00ff95" }}>¿Está seguro que desea registrar el bulto como faltante?</h2>
            <div style={{ display: "flex", flexDirection: "column", gap: "1rem", marginTop: "1.5rem" }}>
                <button onClick={handleConfirmarFaltanteSi} style={registerButton}>Sí</button>
                <button onClick={handleConfirmarFaltanteNo} style={backButtonInsideModal}>No</button>
            </div>
            </div>
        </div>
        )}

        {showOtroFaltanteModal && (
        <div style={modalOverlay}>
            <div style={modalContent}>
            <h2 style={{ color: "#00ff95" }}>¿Desea registrar otro faltante?</h2>
            <div style={{ display: "flex", flexDirection: "column", gap: "1rem", marginTop: "1.5rem" }}>
                <button onClick={handleOtroFaltanteSi} style={registerButton}>Sí</button>
                <button onClick={handleOtroFaltanteNo} style={backButtonInsideModal}>No</button>
            </div>
            </div>
        </div>
        )}

        {showFormularioIrregularidad && (
        <div style={centeredContent}>
            <h1>FORMULARIO DE IRREGULARIDAD</h1>
            <div style={formContainer}>
            <input style={inputStyle} type="text" placeholder="Fecha de la Incidencia" />
            <input style={inputStyle} type="text" placeholder="Código del Bulto" />
            <input style={inputStyle} type="text" placeholder="Número de Acta" />
            <input style={inputStyle} type="text" placeholder="Nombre del Auxiliar" />
            <input style={inputStyle} type="text" placeholder="Nombre del Bulto" />
            <input style={inputStyle} type="text" placeholder="Tipo de Merma" />
            <input style={inputStyle} type="text" placeholder="Cantidad" />
            <div style={cameraBox}>📷</div>
            </div>
            <div style={{ display: "flex", gap: "2rem" }}>
            <button onClick={handleSubmitIrregularidad} style={buttonGreen}>REGISTRAR</button>
            <button onClick={handleRegresarDesdeFormularioIrregularidad} style={backButtonInsideModal}>REGRESAR</button>
            </div>
        </div>
        )}

        {showConfirmacionIrregularidad && (
        <div style={modalOverlay}>
            <div style={modalContent}>
            <h2 style={{ color: "#00ff95" }}>¿Está seguro que desea registrar el bulto como irregularidad?</h2>
            <div style={{ display: "flex", flexDirection: "column", gap: "1rem", marginTop: "1.5rem" }}>
                <button onClick={handleConfirmarIrregularidadSi} style={registerButton}>Sí</button>
                <button onClick={handleConfirmarIrregularidadNo} style={backButtonInsideModal}>No</button>
            </div>
            </div>
        </div>
        )}

        {showConfirmarEntregaCompleta && (
          <div style={modalOverlay}>
            <div style={modalContent}>
              <h3 style={{ color: "#00ff95", textAlign: "center" }}>
                ¿ESTÁ SEGURO QUE DESEA REGISTRAR ESTA SECCIÓN COMO ENTREGA COMPLETA?
              </h3>
              <div style={{ display: "flex", flexDirection: "column", gap: "1rem", marginTop: "1.5rem" }}>
                <button
                  style={registerButton}
                  onClick={() => {
                    setShowConfirmarEntregaCompleta(false);
                    setShowExitoEntregaCompleta(true);
                  }}
                >
                  Sí
                </button>
                <button
                  style={backButtonInsideModal}
                  onClick={() => {
                    setShowConfirmarEntregaCompleta(false);
                    setShowTipoRegistroModal(true); // volver al modal anterior
                  }}
                >
                  No
                </button>
              </div>
            </div>
          </div>
        )}

          {showExitoEntregaCompleta && (
            <div style={modalOverlay}>
              <div style={modalContent}>
                <h2>ENTREGA COMPLETA REGISTRADO</h2>
                <img src="https://img.icons8.com/color/96/000000/ok.png" alt="Success" style={{ margin: '1rem auto' }} />
                <button
                  style={registerButton}
                  onClick={() => {
                    setShowExitoEntregaCompleta(false);
                    // Puedes redirigir, limpiar estados o volver al módulo despacho aquí
                  }}
                >
                  ACEPTAR
                </button>
              </div>
            </div>
          )}

        {showOtroIrregularidadModal && (
        <div style={modalOverlay}>
            <div style={modalContent}>
            <h2 style={{ color: "#00ff95" }}>¿Desea registrar otra irregularidad?</h2>
            <div style={{ display: "flex", flexDirection: "column", gap: "1rem", marginTop: "1.5rem" }}>
                <button onClick={handleOtroIrregularidadSi} style={registerButton}>Sí</button>
                <button onClick={handleOtroIrregularidadNo} style={backButtonInsideModal}>No</button>
            </div>
            </div>
        </div>
        )}

    </div>
  );
}

// --- ESTILOS ---
const mainContainer = {
  minHeight: "100vh",
  background: "linear-gradient(135deg, #000000, #32004f, #ff5722)",
  backgroundSize: "cover",
  color: "#fff",
  display: "flex",
  flexDirection: "column"
};

const navbar = {
  display: "flex",
  justifyContent: "space-between",
  alignItems: "center",
  padding: "1rem 2rem",
  backgroundColor: "#1f1f1f",
};

const navLink = {
  cursor: "pointer",
};

const logoutButton = {
  backgroundColor: "red",
  border: "none",
  padding: "0.5rem 1rem",
  borderRadius: "0.5rem",
  cursor: "pointer",
  fontWeight: "bold",
  color: "#fff"
};

const centeredContent = {
  flex: 1,
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  padding: "2rem"
};

const tableContainer = {
  overflowX: "auto",
  backgroundColor: "#fff",
  borderRadius: "1rem",
  padding: "1rem",
  color: "#000",
  marginBottom: "2rem",
  maxWidth: "100%"
};

const cellStyle = {
  border: "1px solid #ccc",
  padding: "0.5rem",
  textAlign: "center"
};

const inputStyle = {
  width: "100%",
  padding: "0.8rem",
  margin: "0.5rem 0",
  borderRadius: "0.5rem",
  border: "none",
  backgroundColor: "#444",
  color: "#fff",
  outline: "none",
  boxSizing: "border-box"
};

const buttonGreen = {
  padding: "0.8rem 2rem",
  backgroundColor: "#00ff95",
  border: "none",
  borderRadius: "0.5rem",
  color: "#000",
  fontWeight: "bold",
  cursor: "pointer"
};

const modalOverlay = {
    position: "fixed",
    top: 0,
    left: 0,
    width: "100%",
    height: "100%",
    backgroundColor: "rgba(0,0,0,0.6)",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    zIndex: 1000,
  };
  
  const modalContent = {
    backgroundColor: "#333",
    padding: "2rem",
    borderRadius: "1rem",
    textAlign: "center",
    width: "90%",
    maxWidth: "400px",
    color: "#fff",
  };
  
  const registerButton = {
    width: "100%",
    padding: "0.8rem",
    backgroundColor: "#00ff95",
    border: "none",
    borderRadius: "0.5rem",
    fontWeight: "bold",
    color: "#000",
    cursor: "pointer",
  };
  
  const backButtonInsideModal = {
    width: "100%",
    padding: "0.8rem",
    backgroundColor: "red",
    border: "none",
    borderRadius: "0.5rem",
    fontWeight: "bold",
    color: "#fff",
    cursor: "pointer",
  };
  
  const formContainer = {
    backgroundColor: "#333",
    padding: "2rem",
    borderRadius: "1rem",
    width: "90%",
    maxWidth: "600px",
    marginBottom: "2rem",
    display: "flex",
    flexDirection: "column",
    gap: "1rem",
  };
  
  const cameraBox = {
    backgroundColor: "#555",
    height: "150px",
    borderRadius: "0.5rem",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    color: "#bbb",
    fontSize: "2rem",
  };
  

export default Despacho;
